  content: ""; /* Used in a ::after context */
}
[data-shiny-busy-pulse]:not([data-shiny-busy-spinners]).shiny-not-yet-idle::after {
  /* ui.busy_indicators.pulse_options() */
  --_shiny-pulse-background: var(
