    )
  );
  --_shiny-pulse-height: var(--shiny-pulse-height, 5px);
  --_shiny-pulse-speed: var(--shiny-pulse-speed, 1.85s);
  /* Color, sizing, & positioning */
  position: fixed;
  top: 0;
